import React from 'react';
import { useHistory } from 'react-router';
import { DISPLAY_INVESTOR_TYPES } from '../../../../../constants';
import { getNextSection } from '../../../../../utils/validations/Investor-Application-Form/formFlow';

const changeDateFormat = (changeDate) => {
  changeDate = new Date(changeDate);
  changeDate = changeDate.toISOString();
  const dateArr = changeDate.split('T');
  changeDate = dateArr[0].split('-');
  changeDate = changeDate[2] + '-' + changeDate[1] + '-' + changeDate[0];
  return changeDate;
}

const FormCreated = ({ item }) => {

  const statusTypes = new Map([
    ['UNFINISHED', 'Draft'],
    ['UNDER_ASSET_SPONSOR_REVIEW', 'Under Asset Sponsor review'],
    ['ASSET_SPONSOR_REJECTED', 'Rejected by Asset Sponsor'],
    ['ASSET_SPONSOR_ACCEPTED', 'Accepted by Asset Sponsor'],
    ['SUBMITTED', 'Submitted'],
    ['UNDER_REVIEW', 'Under Review'],
    ['ACCEPTED', 'Accepted'],
    ['REJECTED', 'Rejected']
  ]);

  const statusColors = new Map([
    ['UNFINISHED', 'white'],
    ['UNDER_ASSET_SPONSOR_REVIEW', 'yellow'],
    ['ASSET_SPONSOR_REJECTED', 'red'],
    ['ASSET_SPONSOR_ACCEPTED', 'green'],
    ['SUBMITTED', 'blue'],
    ['UNDER_REVIEW', 'purple'],
    ['ACCEPTED', 'green'],
    ['REJECTED', 'red']
  ]);

  //INTEGRATION
  const history = useHistory();

  const doNotShowDownloadIfStatus = ['UNFINISHED']

  const getPDF = () => {
    const baseURL = `${process.env.REACT_APP_API_BASE_URL}:${process.env.REACT_APP_API_PORT}/api/`;
    window.open(`${baseURL}v2/invform/review/${item.id}/pdf`);
  }

  const onClickHandler = () => {

    if(item.status === 'UNFINISHED'){
      const { path } = getNextSection({
        investorType: item.investor_type_no,
        // trustee: item.trustee,
        formId: item.id,
      });
      history.push(path, { fundCode: item?.fund_code });
      // return false
    } else {
      history.push('/form-review', { formId: item.id })
    }

    
    return false;
  }

  return (
    <tr key={item.id} id={item.id}>
      {/* <td><a href="#" onClick={onClickHandler}>{item.fund_title}</a></td> */}
      <td><button onClick={onClickHandler} className="link-button">{item.fund_title}</button></td>
      <td id="status"><p className={statusColors.get(item.status)}>{statusTypes.get(item.status)}</p></td>
      <td>{DISPLAY_INVESTOR_TYPES[item.investor_type_no]}</td>
      <td>{changeDateFormat(item.created_at)}</td>
      <td>{changeDateFormat(item.updated_at)}</td>
      <td>
        {!doNotShowDownloadIfStatus.includes(item.status) ?
        <button className="table-download-pdf" onClick={getPDF}>Download PDF</button>
        : <p>--</p>}
      </td>
    </tr>
  )
}

export default FormCreated;